 /*Extra small devices (portrait phones, less than 576px)*/
 /*No media query since this is the default in Bootstrap*/
.App {
  display: flex;
  flex-wrap: wrap;
  height: calc(100vh - 80px);
  justify-content: center;
}

 .title {
   display: flex;
   font-size: 2rem;
   justify-content: center;
 }

.react-card-flip {
  width: 150px;
  min-height: 100px;
  max-width: 40%;
  flex: 1 0 40%;
  margin: 5px;
  border: 1px solid rgb(151, 151, 151);
  border-radius: 15px;
  /*background-color: rgb(208, 207, 207);*/
  box-shadow: 1px 1px 5px 0;

}

.react-card-back .card {
  background-color: #ffc300;
  background-image: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11 18c3.866 0 7-3.134 7-7s-3.134-7-7-7-7 3.134-7 7 3.134 7 7 7zm48 25c3.866 0 7-3.134 7-7s-3.134-7-7-7-7 3.134-7 7 3.134 7 7 7zm-43-7c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zm63 31c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zM34 90c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zm56-76c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zM12 86c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm28-65c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm23-11c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm-6 60c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm29 22c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zM32 63c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm57-13c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm-9-21c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM60 91c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM35 41c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM12 60c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2z' fill='%23ff4e15' fill-opacity='1' fill-rule='evenodd'/%3E%3C/svg%3E");
  /*color: #0F184A;*/
  color: #07A6B5;
  font-size: 2rem;
  font-weight: bold;
}

.react-card-front .card {
  background-color: #ffffff;
  color: #0F184A;
  font-size: 1rem;
  overflow-wrap: anywhere;
}

.card {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.instructions-wrapper {
  padding: 30px;
}

/*Small devices (landscape phones, 576px and up)*/
@media (min-width: 576px) {
  .react-card-flip {
    flex: 1 0 30%;
    max-width: 30%;
  }

  .react-card-back .card {
    font-size: 2.5rem;
  }

  .react-card-front .card {
    font-size: 1.5rem;
  }
}

/*Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) {
  .title {
    font-size: 3rem;
  }

  .react-card-flip {
    flex: 1 0 25%;
    max-width: 25%;
  }

  .react-card-back .card {
    font-size: 4rem;
  }

  .react-card-front .card {
    font-size: 2rem;
  }
}

/*Large devices (desktops, 992px and up)*/
@media (min-width: 992px) {
  .react-card-flip {
    flex: 1 0 20%;
    max-width: 20%;
  }

  .react-card-back .card {
    font-size: 4rem;
  }

  .react-card-front .card {
    font-size: 2rem;
  }
}

